import React, { useState, useContext } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import Card from "../../shared/components/UIElements/Card";
import Button from "../../shared/components/UIElements/Button";
import Input from "../../shared/components/UIElements/Input";
import ErrorModal from "../../shared/components/UIElements/ErrorModal";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";
import { VALIDATOR_MINLENGTH } from "../../shared/util/validators";
import { useForm } from "../../shared/Hooks/form-hook";
import { useHttpClient } from "../../shared/Hooks/http-hook";
import "./Auth.css";
import { AuthContext } from "../../shared/context/auth-context";

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

const ResetPassword = () => {
  const auth = useContext(AuthContext);
  const query = useQuery();
  const email = query.get("email") || "";
  const token = query.get("token") || "";
  const [success, setSuccess] = useState(null);
  const { isLoading, error, sendRequest, errorHandler } = useHttpClient();
  const [formState, inputHandler] = useForm(
    {
      newPassword: {
        value: "",
        isValid: false,
      },
      confirmPassword: {
        value: "",
        isValid: false,
      },
    },
    false
  );
  const navigate = useNavigate();

  const submitHandler = async (e) => {
    e.preventDefault();
    if (formState.inputs.newPassword.value !== formState.inputs.confirmPassword.value) {
      setSuccess(null);
      return errorHandler("Passwords do not match.");
    }
    try {
      await sendRequest(
        process.env.REACT_APP_BACKEND_URL + "/users/reset-password",
        "POST",
        JSON.stringify({
          email,
          token,
          newPassword: formState.inputs.newPassword.value,
        }),
        { "Content-Type": "application/json",
          Authorization: "Bearer " + auth.token
         }
      );
      setSuccess("Your password has been reset successfully. You can now log in.");
    } catch (err) {}
  };

  if (!email || !token) {
    return (
      <div className="auth-container">
        <div className="auth-content">
          <Card className="auth">
            <h2 className="auth-header">Invalid Link</h2>
            <p>Reset link is invalid or missing required parameters.</p>
            <Button className="switch__button" onClick={() => navigate("/auth")}>Back to Login</Button>
          </Card>
        </div>
      </div>
    );
  }

  return (
    <div className="auth-container">
      <ErrorModal error={error} onClear={errorHandler} />
      <div className="auth-content">
        <Card className="auth">
          {isLoading && <LoadingSpinner />}
          <h2 className="auth-header">Reset Password</h2>
          {success ? (
            <p style={{ color: "green", margin: "1rem 0" }}>{success}</p>
          ) : (
            <form onSubmit={submitHandler}>
              <Input
                id="newPassword"
                element="input"
                label="New Password"
                type="password"
                errorText="Please enter a valid password, at least 6 characters."
                validators={[VALIDATOR_MINLENGTH(6)]}
                onInput={inputHandler}
                placeholder="Enter new password"
              />
              <Input
                id="confirmPassword"
                element="input"
                label="Confirm Password"
                type="password"
                errorText="Passwords must match."
                validators={[VALIDATOR_MINLENGTH(6)]}
                onInput={inputHandler}
                placeholder="Confirm new password"
              />
              <Button type="submit" className="auth__button" disabled={!formState.isValid}>
                Reset Password
              </Button>
            </form>
          )}
          <Button className="switch__button" onClick={() => navigate("/auth")}>Back to Login</Button>
        </Card>
      </div>
    </div>
  );
};

export default ResetPassword; 